import React from "react";
import { AppHeader, AppNavigator } from "./components";
import { AppContext, setUserNavLinks, setShowFullMenu, setUserInfo, setFormLinks, setActivePage } from "./context";
import { fetchInitialData, getAppcuesUserIdentity } from "./remote";

import "./remote";
import "./App.less";
// import { AppNotification } from "./components/AppNotification";
import { AppAnnouncementModal } from "./components/AppAnnouncementModal";

const resource = fetchInitialData();

function App({ appcuesEnabled }: { appcuesEnabled: boolean }) {
    const { state, dispatch } = React.useContext(AppContext);
    const dataProps = resource.payslip.read();

    React.useEffect(() => {
        if (dataProps.navLinks && dataProps.user) {
            dispatch(
                setUserInfo({
                    employeeNo: dataProps.user.EmployeeNo,
                    firstName: dataProps.user.FirstName,
                    lastName: dataProps.user.LastName,
                    companyNo: dataProps.user.CompanyNo || dataProps.user.CompanyNumber,
                })
            );
            dispatch(setUserNavLinks(dataProps.navLinks));
            dispatch(setFormLinks(dataProps.formLinks));
            dispatch(setActivePage(dataProps.activePage));

            const body = document.body || document.getElementsByTagName("body");
            body.classList.add("ss-dashboard-wrapper");
        }

        if (dataProps.user && appcuesEnabled) {
            getAppcuesUserIdentity().then((user: any) => {
                window.Appcues && runAppcuesIdentify(user);
            });
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const runAppcuesIdentify = (user: any) => {
        const roles: string[] = user.userRoles || [];
        const role = roles.length > 0 ? roles.shift() : "";
        let otherRoles = {};
        if (user.userRoles.length > 1) {
            const rolesArray = ["role2", "role3", "role4", "role5"];
            otherRoles = Object.fromEntries(new Map(roles.map((role, index) => [rolesArray[index], role])));
        }
        window.Appcues.identify(user.memberId || user.employeeNo, {
            firstName: user.firstName,
            lastName: user.lastName,
            email: user.emainHome || user.emailWork,
            role,
            ...otherRoles,
            companyId: user.companyNo,
            employeeNo: user.employeeNo,
            country: user.country,
            employeeStartDate: user.employeeStartDate,
            isEmployee: user.isEmployee,
            isAdmin: user.isAdmin,
            isManager: user.isManager,
        });

        window.Appcues.group(user.companyNo, { companyId: user.companyNo, companyName: user.companyName });
    };

    return (
        <>
            <AppHeader title="" onMenuClick={() => dispatch(setShowFullMenu(!state.showFullMenu))} />
            <AppNavigator showFullMenu={state.showFullMenu} onCollapseBtnClick={() => dispatch(setShowFullMenu(!state.showFullMenu))} />
            {state.announcements !== null && state.appLink?.Read ? <AppAnnouncementModal /> : null}
        </>
    );
}

export default App;
